.Modal {
  position: absolute;
  top: 0%;
  right: 0%;
  width: 100%;
  max-width: 600px;
  height: 100vh;
  padding: 16px;
  transform: translate(100%, 0%);
  background-color: $sidebar-bg;
  overflow-y: auto;
  box-shadow: 0 0 15px $text-secondary;

  .closeModalBtn {
    position: absolute;
    width: auto;
    display: flex;
    align-items: baseline;
    top: 16px;
    right: 16px;
    background-color: transparent;
    color: $text-primary;
    font-size: 22px;
  }

  h2 {
    color: $text-primary;
    font-weight: 400;
  }

  form {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;

    span {
      color: $alert;
    }

    label {
      input,
      textarea {
        background-color: $page-bg;
      }

      .receiptSelect {
        margin-top: 3px;
      }
    }
  }

  @media (max-width: 576px) {
    transform: translate(0%, 100%);
    form {
      margin-bottom: 64px;
    }
  }
}
