button {
  position: relative;
  overflow: hidden;
  transition: background 400ms;
  outline: 0;
  border: 0;
  cursor: pointer;
  font-size: 16px;
  border-radius: 3px;
  height: 40px;
  width: 100%;
  cursor: pointer;
}

span.ripple {
  position: absolute; /* The absolute position we mentioned earlier */
  border-radius: 0%;
  transform: scaleX(0);
  animation: ripple 500ms linear;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 600px;

  @keyframes ripple {
    to {
      transform: scaleX(4);
      opacity: 0;
    }
  }
}

.modalBtn {
  font-size: 16px;
  border-radius: 3px;
  height: 40px;
  width: 100%;
  margin-top: 24px;
  color: $text-primary;
  background-color: $primary;
  cursor: pointer;
}

.primaryBtn {
  color: $text-primary;
  background-color: $primary;
}

.secondaryBtn {
  margin-top: 24px;
  color: $text-primary;
  background-color: $secondary;
}

.grayBtn {
  margin-top: 0;
  color: $text-primary;
  background-color: $sidebar-bg;
}

.dangerBtn {
  color: $text-primary;
  background-color: $alert;
}

.loadingBtn {
  height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  border-radius: 3px;
  margin-top: 32px;
  color: $text-primary;
  background-color: $sidebar-bg;
  cursor: pointer;

  &::before {
    display: inline-block;
    position: relative;
    content: "";
    height: 20px;
    width: 20px;
    border-radius: 100px;
    border: 2px solid $text-secondary;
    border-top: 2px solid $primary;
    animation: loader 1s infinite linear;

    @keyframes loader {
      from {
        transform: rotateZ(0deg);
      }
      to {
        transform: rotateZ(360deg);
      }
    }
  }
}
