ul {
  height: 100%;
  width: 224px;
  list-style: none;
  margin-top: 12px;
  transition: width 0.7s ease-in-out;

  a {
    text-decoration: none;

    li {
      height: 40px;
      width: 100%;
      padding: 0 12px;
      display: flex;
      align-items: center;
      background-color: $sidebar-bg;
      border-radius: 2px;
      cursor: pointer;
      transition: background-color 0.2s ease-in-out;

      i {
        font-size: 20px;
        padding-right: 12px;
        color: $text-secondary;
        transition: color 0.2s ease-in-out;
      }

      span {
        font-size: 14px;
        font-weight: 500;
        letter-spacing: 2px;
        color: $text-primary;
        opacity: 1;
        transition: opacity 0.3s ease-in-out;
      }
    }

    &.active {
      li {
        background-color: $page-bg;
        transition: width 0.6s ease-in-out;

        i {
          color: $primary;
        }
      }
    }
  }
}
