label {
  width: 100%;
  color: #f1f5f9;
  margin-top: 24px;
}

label input,
label textarea,
label select {
  height: 40px;
  width: 100%;
  font-size: 16px;
  padding-left: 6px;
  margin-top: 3px;
  margin-bottom: 1px;
  border: 2px solid transparent;
  border-radius: 3px;
  color: #f1f5f9;
  background-color: #0f172a;
  outline: none;
  transition: all 0.3s ease;
  background-color: #1e293b;
  appearance: none;
}

label input:focus,
label textarea:focus,
label select:focus {
  border: 2px solid #9d174d;
  opacity: 0.9;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

textarea {
  max-height: 150px;
  min-height: 70px;
  max-width: 100%;
  min-width: 100%;
  background-color: #1e293b;
  padding: 8px 0 0 6px;
}

.selectDropdown {
  position: relative;
  width: 100%;
  border-radius: 3px;
}

.selectDropdown select option:active {
  background-color: #9d174d;
}

.selectDropdown:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 8px;
  width: 0;
  height: 0;
  border-top: 5px solid #aaa;
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
}

.Modal {
  position: absolute;
  top: 0%;
  right: 0%;
  width: 100%;
  max-width: 600px;
  height: 100vh;
  padding: 16px;
  transform: translate(100%, 0%);
  background-color: #0f172a;
  overflow-y: auto;
  box-shadow: 0 0 15px #64748b;
}

.Modal .closeModalBtn {
  position: absolute;
  width: auto;
  display: flex;
  align-items: baseline;
  top: 16px;
  right: 16px;
  background-color: transparent;
  color: #f1f5f9;
  font-size: 22px;
}

.Modal h2 {
  color: #f1f5f9;
  font-weight: 400;
}

.Modal form {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}

.Modal form span {
  color: #dc2626;
}

.Modal form label input,
.Modal form label textarea {
  background-color: #1e293b;
}

.Modal form label .receiptSelect {
  margin-top: 3px;
}

@media (max-width: 576px) {
  .Modal {
    transform: translate(0%, 100%);
  }
  .Modal form {
    margin-bottom: 64px;
  }
}

.selectCategory {
  height: 40px;
  width: 100%;
  margin-top: 0;
  margin-top: 3px;
}

.selectCategory .selectCategory__menu {
  top: 32px;
  background-color: #1e293b;
  color: #f1f5f9;
}

.selectCategory .selectCategory__menu .selectCategory__menu-list {
  padding-top: 0;
  border-radius: 3px;
}

.selectCategory .selectCategory__menu .selectCategory__option {
  transition: all 0.5 ease;
}

.selectCategory .selectCategory__menu .selectCategory__option:hover {
  background-color: inherit;
  opacity: 0.8;
}

.selectCategory .selectCategory__menu .selectCategory__option--is-focused {
  background-color: inherit;
}

.selectCategory .selectCategory__menu .selectCategory__option--is-selected {
  background-color: #9d174d;
}

.selectCategory .selectCategory__value-container {
  height: 36px;
  display: flex;
  align-items: center !important;
  justify-content: flex-start;
  flex-wrap: wrap;
  border-radius: 3px;
  border: 2px solid transparent;
  color: #f1f5f9;
  padding-left: 2px;
}

.selectCategory .selectCategory__value-container .selectCategory__single-value {
  padding: 0 0 16px 0;
  color: #f1f5f9;
}

.selectCategory .selectCategory__value-container .selectCategory__placeholder {
  padding: 0 0 16px 0;
  color: #64748b;
}

.selectCategory .selectCategory__input-container {
  height: auto;
  color: #f1f5f9;
}

.selectCategory .selectCategory__input-container input {
  padding-left: 16px;
  padding-bottom: 6px;
}

.selectCategory .selectCategory__control {
  box-sizing: border-box;
  width: 100%;
  background-color: #1e293b;
  color: #f1f5f9;
  border: 2px solid transparent;
  transition: all 0.3s ease;
}

.selectCategory .selectCategory__control:hover {
  border: 2px solid #9d174d;
}

.selectCategory .selectCategory__control--is-focused {
  box-shadow: none;
  border: 2px solid #9d174d;
  outline: none;
  transition: all 0.3s ease;
}

.selectCategory .selectCategory__control--menu-is-open {
  box-shadow: none;
  border: 2px solid #9d174d;
  outline: none;
  transition: all 0.3s ease;
}

.ReactTags__selected {
  margin-top: 12px;
}

.ReactTags__selected .tag-wrapper {
  color: #f1f5f9;
  background-color: #9d174d;
  padding: 5px 7px;
  border-radius: 3px;
  margin-right: 5px;
}

.ReactTags__selected .tag-wrapper button {
  width: 18px;
  background-color: transparent;
  padding: 0 0 0 7px;
}

.ReactModal__Content {
  transition: all 0.6s ease-in-out;
}

.ReactModal__Content--after-open {
  transform: translate(0%, 0%);
}

.ReactModal__Content--before-close {
  transform: translate(100%, 0%);
}

@media (max-width: 576px) {
  .ReactModal__Content--before-close {
    transform: translate(0%, 100%);
  }
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}

button {
  position: relative;
  overflow: hidden;
  transition: background 400ms;
  outline: 0;
  border: 0;
  cursor: pointer;
  font-size: 16px;
  border-radius: 3px;
  height: 40px;
  width: 100%;
  cursor: pointer;
}

span.ripple {
  position: absolute;
  /* The absolute position we mentioned earlier */
  border-radius: 0%;
  transform: scaleX(0);
  animation: ripple 500ms linear;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 600px;
}

@keyframes ripple {
  to {
    transform: scaleX(4);
    opacity: 0;
  }
}

.modalBtn {
  font-size: 16px;
  border-radius: 3px;
  height: 40px;
  width: 100%;
  margin-top: 24px;
  color: #f1f5f9;
  background-color: #9d174d;
  cursor: pointer;
}

.primaryBtn {
  color: #f1f5f9;
  background-color: #9d174d;
}

.secondaryBtn {
  margin-top: 24px;
  color: #f1f5f9;
  background-color: #08849e;
}

.grayBtn {
  margin-top: 0;
  color: #f1f5f9;
  background-color: #0f172a;
}

.dangerBtn {
  color: #f1f5f9;
  background-color: #dc2626;
}

.loadingBtn {
  height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  border-radius: 3px;
  margin-top: 32px;
  color: #f1f5f9;
  background-color: #0f172a;
  cursor: pointer;
}

.loadingBtn::before {
  display: inline-block;
  position: relative;
  content: "";
  height: 20px;
  width: 20px;
  border-radius: 100px;
  border: 2px solid #64748b;
  border-top: 2px solid #9d174d;
  animation: loader 1s infinite linear;
}

@keyframes loader {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(360deg);
  }
}

.divider {
  width: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  color: #64748b;
  margin: 12px 0;
}

.divider:before, .divider:after {
  content: "";
  flex: 1;
  border-bottom: 1px solid #64748b;
}

.divider:not(:empty)::before {
  margin-right: 6px;
}

.divider:not(:empty)::after {
  margin-left: 6px;
}

nav {
  position: fixed;
  display: flex;
  align-items: baseline;
  flex-direction: column;
  height: 100vh;
  width: 256px;
  padding: 16px;
  background-color: #0f172a;
  transition: all 0.5s ease-in-out;
}

nav img {
  width: 46px;
  height: 46px;
  padding: 0 6px;
  margin-bottom: 32px;
}

nav .buttonContainer {
  height: 40px;
  width: 224px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  text-align: center;
  margin-bottom: 16px;
  border: 2px solid #9d174d;
  border-radius: 3px;
  transition: all 0.5s ease-in-out;
}

nav .buttonContainer button {
  width: 46px;
  padding: 0;
  margin: 0;
  font-size: 24px;
  z-index: 1;
  transition: all 0.5s ease-in-out;
}

nav .buttonContainer span {
  position: absolute;
  width: 224px;
  padding-left: 46px;
  color: #f1f5f9;
  transition: opacity 0.3s ease-in-out;
}

nav .buttonContainer:hover button {
  width: 100%;
}

nav .buttonContainer:hover span {
  opacity: 0;
}

nav .sidebarPages {
  height: 16px;
  color: #64748b;
  padding-left: 14px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 13px;
  transition: all 0.5s ease-in-out;
}

nav .collapseButton {
  height: 46px;
  width: 46px;
  background-color: #0f172a;
  border: none;
  align-self: flex-end;
  color: #f1f5f9;
  font-size: 18px;
  transition: all 0.5s ease;
}

nav .collapseButton:hover {
  cursor: pointer;
}

nav.collapsed {
  width: 78px;
}

nav.collapsed .sidebarPages {
  visibility: hidden;
  opacity: 0;
}

nav.collapsed ul {
  height: 100%;
  width: 60px;
  list-style: none;
  margin-top: 12px;
}

nav.collapsed ul a li {
  width: 46px;
}

nav.collapsed ul a li span {
  opacity: 0;
}

nav.collapsed .buttonContainer {
  width: 46px;
}

nav.collapsed .buttonContainer span {
  opacity: 0;
  transition: 0.3s ease-in-out;
}

nav.collapsed .collapseButton {
  transform: rotateZ(180deg);
}

ul {
  height: 100%;
  width: 224px;
  list-style: none;
  margin-top: 12px;
  transition: width 0.7s ease-in-out;
}

ul a {
  text-decoration: none;
}

ul a li {
  height: 40px;
  width: 100%;
  padding: 0 12px;
  display: flex;
  align-items: center;
  background-color: #0f172a;
  border-radius: 2px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

ul a li i {
  font-size: 20px;
  padding-right: 12px;
  color: #64748b;
  transition: color 0.2s ease-in-out;
}

ul a li span {
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 2px;
  color: #f1f5f9;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

ul a.active li {
  background-color: #1e293b;
  transition: width 0.6s ease-in-out;
}

ul a.active li i {
  color: #9d174d;
}

.mobileNav {
  position: fixed;
  bottom: 0;
  height: 50px;
  width: 100%;
  border-top: 2px solid #1e293b;
  background-color: #0f172a;
  z-index: 10;
}

.mobileNav ul {
  margin-top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.mobileNav ul a.active li {
  background-color: transparent;
}

.mobileNav ul a.active li::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: #9d174d;
  border-radius: 50%;
  animation: ripple 0.5s ease-out;
  opacity: 0;
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.mobileNav ul li {
  width: 40px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  background-color: none;
}

.mobileNav ul li.buttonContainer {
  width: 25%;
  height: 48px;
  border: 2px solid #9d174d;
  border-radius: 3px;
}

.mobileNav ul li.buttonContainer button {
  height: 100%;
  width: 100%;
  background-color: transparent;
  color: #9d174d;
  font-size: 22px;
}

.mobileNav ul li i {
  padding: 0;
}

