.dashboard {
  height: calc(100% - 64px);
  width: 100%;
  overflow-y: auto;
  padding: 0 16px 16px 16px;
}

.dashboard .chartContainer {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 16px;
  padding: 16px 0;
}

.dashboard .chartContainer .chart {
  width: 100%;
  text-align: center;
  height: 100px;
  border-radius: 3px;
  border: 1px solid #9d174d;
  background-color: #0f172a;
